.plant-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
  }
  
  .plant-details-header {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  
  .back-button {
    cursor: pointer;
  }
  
  .plant-details-info {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  
  .watering-log ul {
    list-style-type: none;
    padding: 0;
  }
  
  .watering-reminder-button,
  .remove-plant-button {
    cursor: pointer;
    margin-top: 10px;
  }